import React, { ReactNode } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableRow,
} from '@material-ui/core';

import { IStats } from '../../data/stats/stats';

interface IStatsRowProps {
  label: string;
  value?: any;
  link?: string;
  format?: (value: any) => ReactNode;
}

const StatsRow = ({
  label,
  link,
  value,
  format = (value) => value,
}: IStatsRowProps) => (
  <TableRow>
    <TableCell>{label}</TableCell>
    <TableCell>
      {link ? <a href={link}>{format(value)}</a> : format(value)}
    </TableCell>
  </TableRow>
);

interface IStatsTableProps {
  stats: IStats[];
}

const StatsTable = ({ stats }: IStatsTableProps) => (
  <TableContainer>
    <Table width="600px">
      <TableBody>
        {stats.map((stat: IStats) => (
          <StatsRow
            format={stat.format}
            key={stat.label}
            label={stat.label}
            link={stat.link}
            value={stat.value}
          />
        ))}
      </TableBody>
    </Table>
  </TableContainer>
);

export default StatsTable;
